<template>
<div id="users">
  <el-table :data="tableData">
    <el-table-column prop="username" label="用户名" :width="80"></el-table-column>
    <el-table-column prop="email" label="邮箱"></el-table-column>
    <el-table-column prop="phone" label="手机"></el-table-column>
    <el-table-column prop="" label="操作">
      <div slot-scope="{row}">
        <el-button-group>
          <el-button type="default" round size="mini">编辑</el-button>
          <el-button type="default" round size="mini">删除</el-button>
          <el-button type="default" round size="mini">重置密码</el-button>
        </el-button-group>
      </div>
    </el-table-column>
  </el-table>
  <div style="margin-top:20px">

    <el-pagination :current-page.sync="currentpage" :page-size="pagesize"  @current-change="getUsersList" :total="total" background layout=" prev, pager, next">
    </el-pagination>
    <!-- <el-pagination
        @size-change="sizeChange"
        :page-sizes="[20, 40, 80, 100]"
        
        @current-change="currentChange"
        :current-page="currentPage"
        
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalNum" background>
      </el-pagination> -->
  </div>

</div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [],
      total: 0,
      pagesize: 4,
      currentpage: 1
    };
  },
  created() {
    this.getUsersList();
  },
  methods: {
    getUsersList() {
      this.$http
        .get(
          `/users/list?pagesize=${this.pagesize}&currentpage=${
            this.currentpage
          }`
        )
        .then(result => {
          this.total = result.data.total;
          this.tableData = result.data.data;
        });
    }
  }
};
</script>



